// Body
$body-bg: #ffffff;

// Typography
$font-family-sans-serif: 'Nunito', sans-serif;
$font-size-base: 0.9rem;
$line-height-base: 1.6;

// Colors
$blue-light: #f1fcff;
$blue: #3596e6; //#1a6fb4
$blue-med: #015091;
$blue-dark: #124772;
$blue-darker: #0d3555;
/* $blue: #4c9bdb;
$blue-dark: #1972bb;
$blue-darker: #104875;
$blue-med: #6cb2eb;
$blue-light: #f8fcff; */
$blue-gray: #adcee8;
$indigo: #6574cd;
$purple: #9561e2;
$pink: #f66d9b;
$red: #9e4c4b;
$danger_red: #a14f4e;
$red-light: #f8d9d9;
$red-lighter: #f5e0e0;
$orange: #df7935;
$orange-hover: #e06e23;
$orange-light: #f8e5e3;
$orange-dark: #943d31;
$yellow: #ffed4a;
$yellow-light: #f7efae;
$green: #4c864c;
$success_green: #518d54;
$green-light: #bef1be;
$green-lighter: #d2f1d2;
$green-dark: #1b631b;
$teal: #4dc0b5;
$cyan: #6cb2eb;
$default: #647c96;

$theme-colors: (
    "primary": $blue,
    "secondary": $orange,
    "danger": $danger_red,
    "success": $success_green,
    "default": $default,
    "primary-dark": $blue-dark
);

$primary-color: $blue;
$secondary-color: $orange;
$danger-color: $red;

$border-color: #dee2e6;

$text-gray: #7a7979;
$text-gray-dark: #5d5a5a;

.text-primary-dark {
    color: $blue-dark;
}
.text-default {
    color: $default;
}
.text-blue-light {
    color: $blue-light;
}
.text-yellow {
    color: $yellow;
}
.text-yellow-light {
    color: $yellow-light;
}
.text-orange {
    color: $orange !important;
}
.text-gray {
    color: $text-gray;
}
.text-gray-dark {
    color: $text-gray-dark;
}
a.text-orange:hover {
    color: $orange-hover;
}
.text-green {
    color: $green;
}
a.text-green:hover {
    color: $green-dark;
}
.text-red {
    color: red;
}


.bg-default {
    background: $default !important;
    background-color: $default !important;
}
.bg-default-light {
    background: rgba($default, .4) !important;
    background-color: rgba($default, .4) !important;
}
.bg-default-lighter {
    background: rgba($default, .1) !important;
    background-color: rgba($default, .1) !important;
}
.bg-primary-light {
    background: rgba($primary-color, .1) !important;
    background-color: rgba($primary-color, .1) !important;
}
.bg-secondary-light {
    background: rgba($secondary-color, .1) !important;
    background-color: rgba($secondary-color, .1) !important;
}
.bg-orange-light {
    background: $orange-light !important;
    background-color: $orange-light !important;
}
.bg-orange {
    background: $orange !important;
    background-color: $orange !important;
}
.bg-blue-light {
    background: $blue-light !important;
    background-color: $blue-light !important;
}

.bg-blue-med {
    background: $blue-med !important;
}

.bg-green {
    background: $green !important;
    background-color: $green !important;
}
.bg-green-light {
    background: rgba($green, .1) !important;
    background-color: rgba($green, .1) !important;
}
.bg-green-lighter {
    background: $green-lighter !important;
    background-color: $green-lighter !important;
}

.bg-red-light {
    background: $red-light !important;
    background-color: $red-light !important;
}
.bg-red-lighter {
    background: $red-lighter !important;
    background-color: $red-lighter !important;
}



.btn-primary {
    background: $blue;
}

.btn-danger {
    background: $danger-color;
}

.btn-secondary {
    background: $orange;
}

.border-orange {
    border: 1px solid $orange !important;
}

$badge_blue: #416c8f;
$badge_blue_light: #7daed6;
$badge_green: #5d975d;
$badge_orange: #b16b3c;
$badge_red: #7e3736;
$badge_default: #496b90;


.badge.bg-primary {
    //background: linear-gradient(122deg, $badge_blue, $blue) !important;
    background: $badge_blue;
}
.badge.bg-success {
    //background: linear-gradient(122deg, $badge_green, $green) !important;
    background: $badge_green,;
}
.badge.bg-orange {
    //background: linear-gradient(122deg, $badge_orange, $orange) !important;
    background: $badge_orange;
}
.badge.bg-danger {
    //background: linear-gradient(122deg, $badge_red, $red) !important;
    background: $badge_red;
}
.badge.bg-blue-light {
    //background: linear-gradient(122deg, $badge_blue_light, $blue_light) !important;
    background: $badge_blue;
}
.badge.bg-default {
    //background-image: linear-gradient(122deg, $badge_default 0%, $default 100%) !important;
    background-image: $badge_default;
}




