/// MODALS /////
.modal {

    padding-right: 0px !important;

    .modal-lg, .modal-xl {
        top: 25px;

        .modal-content {
            margin-bottom: 50px;
        }
    }

    .modal-xxl {
        max-width: 95% !important;
    }
    .modal-xl-1400 {
        max-width: 1400px;
    }

    .modal-dialog .modal-content .modal-header {
        border-top-left-radius: .2rem !important;
        border-top-right-radius: .2rem !important;
    }

    .modal-dialog {
        margin: 0 auto !important;
    }


    .modal-header:not(.bg-success):not(.bg-danger) {
        background: $white;
        padding: .5rem .5rem .5rem 1rem !important;

        h1,
        h2,
        h3,
        h4,
        h5 {
            color: $primary;
        }

        .fa-times {
            margin-right: 15px;
        }
    }

    /* .modal-header.bg-success, .modal-header.bg-danger {

        h1,
        h2,
        h3,
        h4,
        h5 {
            color: $white !important;
        }

        .fa-times {
            color: $white !important;
        }

    } */

    .modal-body {
        padding: 2rem;
    }

    .modal-dialog .modal-content {
        border-top-left-radius: .2rem !important;
        border-top-right-radius: .2rem !important;
        border-bottom-left-radius: .2rem !important;
        border-bottom-right-radius: .2rem !important;
        border:  2px solid $blue-light !important;
    }

}

.modal-backdrop.show {
    background: $blue-darker;
    opacity: 0.7;
}

body,
body.modal-open {
    padding-right: 0px !important;
    overflow: visible !important;
}

*::-webkit-scrollbar {
    width: .5em;
    height: .5em;
}

*::-webkit-scrollbar-track {
    display: none;
}

*::-webkit-scrollbar-thumb {
    background-color: rgba($primary, 0.4);
    border-radius: 5px;
}

* {
    scrollbar-width: thin;
    /* "auto" or "thin"  */
    scrollbar-color: rgba($primary, 0.4) $blue-light;
}



