a.text-gray:hover {
    color: rgb(139, 139, 139);
}

.dropdown-menu {
    padding: .6rem !important;
}

.dropdown-item {
    margin-top: 5px;
    padding: .4rem;
    color: $primary;

    i {
        color: $primary;
    }

    &:hover {
        color: $primary;
        background: $blue-light;
        border-radius: 5px;
        box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);

        i {
            color: $primary;
        }

    }

}

.border-primary {
    border: 1px solid $primary;
}
.border-top-primary {
    border-top: 1px solid $primary;
}
.border-bottom-primary {
    border-bottom: 1px solid $primary;
}
.border-gray {
    border: 1px solid $border-color;
}
.border-top-gray {
    border-top: 1px solid $border-color;
}
.border-bottom-gray {
    border-bottom: 1px solid $border-color;
}

.rounded-top {
    border-top-left-radius: .7rem;
    border-top-right-radius: .7rem;
}
.rounded-bottom {
    border-bottom-left-radius: .7rem;
    border-bottom-right-radius: .7rem;
}
.rounded-left {
    border-top-left-radius: .7rem;
    border-bottom-left-radius: .7rem;
}
.rounded-right {
    border-top-right-radius: .7rem;
    border-bottom-right-radius: .7rem;
}
.rounded-top-left {
    border-top-left-radius: .7rem;
}
.rounded-top-right {
    border-top-right-radius: .7rem;
}
.rounded-bottom-left {
    border-bottom-left-radius: .7rem;
}
.rounded-bottom-right {
    border-bottom-right-radius: .7rem;
}

.list-group-item-action:not(.active):hover {
    background-color: rgba($blue-light, 0.4);
}

.badge:not(.badge-pill) {
    border-radius: 3px;
    padding: .4rem;
    margin-left: .5rem;
    margin-right: .5rem;
}

@media (min-width: 1200px) {
    .fixed-sn main {
        margin-left: 20% !important;
        margin-right: 20% !important;
    }
}

.overflow-x-hidden {
    overflow-x: hidden;
}
.overflow-x-scroll {
    overflow-x: scroll;
}
.overflow-y-hidden {
    overflow-y: hidden;
}
.overflow-y-scroll {
    overflow-y: scroll;
}

// toastr
#toast-container > div {
    opacity: 1 !important;
}


.badge {
    padding: 0.25em 0.45em 0.32rem;
}

.badge-pill {
    padding: 0.45em 0.55em 0.32rem 0.55em;
}

.border-2 {
    border-width: 2px !important;
}

.border-3 {
    border-width: 3px !important;
}

// datatables


.table.responsive {
    width: 100%;
}

.table {
    color: $text-gray;
}

.dataTables_wrapper {

    .dt-buttons .btn {
        padding: .2rem .4rem;
        font-size: .8rem;
    }

    .dataTable {

        border-radius: 5px;

        &.table {
            border-radius: 5px;
            // border-left: none !important;
            // border-right: none !important;

            &.table-hover tbody tr:hover {
                background-color: $blue-light;
            }

            th {
                //background: $blue-light;
                padding: .6rem 0rem .6rem 0.4rem;
                color: $blue;
                vertical-align: middle;
                font-size: .9rem;
                border-bottom: 1px solid $border-color;
                font-weight: normal;

                &:first-child {
                    border-top-left-radius: 5px;
                }
                &:last-child {
                    border-top-right-radius: 5px;
                }

                // &:not(:first-child) {
                //     border-left: none !important;
                // }

                // &:not(:last-child) {
                //     border-right: none !important;
                // }

            }
            td {
                padding: 0.4rem;
                vertical-align: middle;
                font-size: .9rem;
                color: $text-gray;

                &:not(:first-child) {
                    border-left: none !important;
                }

                &:not(:last-child) {
                    border-right: none !important;
                }

                &.small {
                    font-size: .8rem !important;
                }


            }
            tr:last-child td {
                &:first-child {
                    border-bottom-left-radius: 5px;
                }
                &:last-child {
                    border-bottom-right-radius: 5px;
                }
            }

        }

        /* .sorting:before, .sorting_asc:before, .sorting_desc:before, .sorting_asc_disabled:before, .sorting_desc_disabled:before,
        .sorting:after, .sorting_asc:after, .sorting_desc:after, .sorting_asc_disabled:after, .sorting_desc_disabled:after {
            color: $blue;
            font-size: 1rem;
            line-height: 1rem;
        } */
    }

    .dataTables_length {
        font-size: .9rem;
    }

    .dataTables_info {
        font-size: .8rem;
    }

    .page-link {
        padding: .3rem .6rem;
        font-size: .7rem !important;
        line-height: 1 !important;
    }


}

@media (max-width: 768px) {

    .dataTables_wrapper {

        .dataTable {

            &.table {

                th {
                    padding: .3rem 0rem .3rem 0.2rem !important;
                    font-size: .7rem !important;

                    &:last-child {
                        border-right: none;
                    }

                }
                td {
                    font-size: .7rem !important;

                    &.small {
                        font-size: .7rem !important;
                    }

                    &:not(:first-child) {
                        padding: 0.2rem !important;
                    }

                    &:before {
                        top: 50% !important;
                    }

                    .dtr-details {
                        width: 100%;
                    }


                }

            }

        }

        .page-link {
            padding: .2rem .4rem;
            font-size: .6rem !important;
            //line-height: 1 !important;
        }

    }

    .dataTables_filter input {
        width: 120px !important;
        padding: 4px 0 2px 3px;
        height: 25px;
        margin-top: 8px;
    }

    .dataTables_length label {
        font-size: .7rem;
        margin-top: 8px;

        select {
            padding: 2px 25px 1px 6px;
        }

    }

    .dataTables_info {
        margin-bottom: 5px;
    }

}

.modal-1000 {
